.TranslateButton {
    background-color: #3762e3;
    display: flex;
    align-items: center;
    gap: 10px;
    color: white;
    font-weight: 600;
    padding: 10px 25px;
    border: 1px solid #a0b8ff;
    border-radius: 8px;
    font-size: 1rem;
    transition: 0.3s ease-in-out;
}
.TranslateButton:hover {
    cursor: pointer;
    background-color: black;
    border: 1px solid #4a4a4a;
}